'use client'

import { PrivateHeader } from "@/components/privateHeader";
import { StatisticsCard } from "@/components/StatisticsCard";
import { Table, TableBody, TableCaption, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { getAllRegister } from "@/services/register.request";
import { useQuery } from "@tanstack/react-query";

export default function Register() {
    const { data: register } = useQuery({
        queryKey: ['disciplines'],
        queryFn: getAllRegister,
    })
    return (
        <main className="w-full min-h-screen p-8">
            <PrivateHeader pageTitle="Registro de Rotina" />
            <div className="w-full flex flex-row gap-4 mt-4">
                <StatisticsCard title="Dias de Estudo">
                    <h1 className="text-7xl font-black">12</h1>
                </StatisticsCard>
                <StatisticsCard title="Média de Horas p/dia">
                    <h1 className="text-7xl font-black">8:00:00</h1>
                </StatisticsCard>
                <StatisticsCard title="Melhor Sequência">
                    <h1 className="text-7xl font-black">2</h1>
                </StatisticsCard>
            </div>
            <div className="w-full mt-4">
                <StatisticsCard title="Registro de Aulas">
                    <Table className="w-full">
                        <TableCaption>Suas sessões de estudo. Continue assim!</TableCaption>
                        <TableHeader>
                            <TableRow className="grid grid-cols-3">
                                <TableHead className="flex justify-center">Data</TableHead>
                                <TableHead className="flex justify-center">Total de Videos Assistidos</TableHead>
                                <TableHead className="flex justify-center">Tempo de Estudo</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {register && register.map((register) => (
                                <TableRow key={register.id} className="grid grid-cols-3">
                                    <TableCell className="flex justify-center">{register.date.toLocaleDateString('pt-BR')}</TableCell>
                                    <TableCell className="flex justify-center">12:00:00</TableCell>
                                    <TableCell className="flex justify-center font-medium">{0}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </StatisticsCard>
            </div>
        </main>
    )
}